import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { LogindataService } from "../../logindata.service";
import { UserSchema } from '../../app.UserInterface';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  logindetails: any={};
  constructor(private http: HttpClient, private router: Router,private _loginService: LogindataService) {
    
   }

  ngOnInit() {
    
  }
  onSubmit(formValue: any){
    console.log(formValue);
    this.http.get('https://mutual-fund.herokuapp.com/users/auth/'+formValue.Lemail).subscribe((data:UserSchema[]) => {
      this.logindetails = data;
             console.log(data);
          if (this.logindetails != null){
            // console.log("This Category have Schemes please delete it ")
            if(formValue.password == "wipro@123"){
             // console.log("this is loging data"+ this.logindetails._id )
              this._loginService.putAdminname(this.logindetails._id,this.logindetails.emailID);
              this.router.navigate(['dashboard', {outlets: {'admin': ['admin']}}]);

            }else{
              console.log("this is loging data"+ this.logindetails.password );
              if(formValue.password == this.logindetails.password){
                //  console.log("this is loging data"+ this.logindetails._id )
                this._loginService.putUsername(this.logindetails._id,this.logindetails.emailID);
               
                  this.router.navigate(['dashboard', {outlets: {'user': ['user']}}]);
  
                }
                else{
                  
                  window.alert("Please check the password ..! ")
                
                
                }
            }
            
          }else{
            
            window.alert("The user data not present ..! ")
          
          
          }
        },
          err => console.log(err)
        );

    

  }

}



