import { Injectable} from '@angular/core';


@Injectable()
export class LogindataService {

  Usernameid: string;
  Useremail: String;
  Adminuser: boolean = false;
  Schemename: string;
  Basketcount: number=0;
  updateCategory: boolean = false;
  private userlogedin;
  constructor() {

    this.userlogedin=false;

   }

  

  getUsername() {
   
    return this.Usernameid;
  }
  putUsername(username: any,useremail: any) {
    this.userlogedin=true;
    this.Useremail=useremail;
    this.Usernameid=username;
    
    
  }

  putAdminname(username: any,useremail: any) {
    this.userlogedin=true;
    this.Useremail=useremail;
    this.Usernameid=username;
    this.Adminuser=true;
    
  }

  putSchemename(schemename: any){
    this.Schemename=schemename;
  }

  putupdateCategory(enable: any){
    this.updateCategory=enable;
  }

  putaddToBasket(count: number){
    this.Basketcount=this.Basketcount+count;
  }
  putremoveFromBasket(){
    this.Basketcount--;
  }

  getaddToBasket(){
    return this.Basketcount;
  }

  getUserlogedin(){
    return this.userlogedin;
  }
  getSchemename(){
    return this.Schemename;
  }
  getUseremail(){
    return this.Useremail;
  }

  getAdminlogin(){
    return this.Adminuser;
  }

  getupdateCategory(){
    return this.updateCategory;
  }
  
 
}



