import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from "@angular/common";
import { Window } from 'selenium-webdriver';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  constructor(private http: HttpClient, private router: Router, private location: Location) { }

  ngOnInit() {
  }
  onSubmit(formValue: any) {

    console.log(formValue);

    //   console.log("newUser = " + newAdminUser.Email_ID);


    if (formValue.pwd == formValue.repwd) {
      this.http.post('http://mutual-fund.herokuapp.com/users/createuser', formValue).subscribe(
        res => {
          console.log(res);
        },
        err => {
          console.log("Error occured");
        }
      );


      this.http.post('http://mutual-fund.herokuapp.com/send', formValue).subscribe(
        res => {
          console.log(res);
        },
        err => {
          console.log("Error occured");
        }
      );

      alert("Account created sucessfully..")

      this.router.navigate(['/site', {outlets: {site: ['login']}}]);
    //window.location.reload();
    }
    else {
      alert("Password Mismatch")
    }


  }

}



