import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { FileUploader } from 'ng2-file-upload';
import { LogindataService } from '../../../../logindata.service'


@Component({
  selector: 'app-update-category',
  templateUrl: './update-category.component.html',
  styleUrls: ['./update-category.component.css']
})
export class UpdateCategoryComponent implements OnInit {

  category: any = {};
  id: string;

  constructor(private http: HttpClient, private router: Router, private route: ActivatedRoute, private _logindata: LogindataService) { }

  ngOnInit() {
    this.route.params.forEach((params: Params) => {
      // console.log(params['_id']);
      this.id = params['_id'];
    });
    this.http.get('https://mutual-fund.herokuapp.com/users/updateCategory/'+this.id).subscribe((data: any[]) => {
      // console.log(data);
      this.category = data;
    },
      err => console.log(err)
    );

  }

  onSubmitUpdate(formValue: any){

    

    let updatedcategory = {
      Category_name: formValue.Category_name ,
      Category_description: formValue.Category_description 
         
        };
        this.http.post('https://mutual-fund.herokuapp.com/users/updateCategory/'+this.id, updatedcategory ).subscribe(
          res => {
          console.log(res);
        },
        err => {
          console.log("Error occured ");
        }
      );

      window.alert("Category Updated sucessfully..!");
    this.router.navigate(['dashboard', {outlets: {'admin': ['categoryadmin']}}]);
    
  }

}



