import { Routes, RouterModule } from "@angular/router";
import { NgModule } from '@angular/core';
import { SiteComponent } from './site/site.component';
import { SlidshowComponent } from './slidshow/slidshow.component';
import { AboutComponent } from './site/about/about.component';
import { ServicesComponent } from './site/services/services.component';
import { LoginComponent } from './site/login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { UserComponent } from './dashboard/user/user.component';
import { FundsComponent } from './dashboard/user/funds/funds.component';
import { CategoryComponent } from './dashboard/user/funds/category/category.component';
import { NavhistoryComponent } from './dashboard/user/navhistory/navhistory.component';
import { BasketComponent } from './dashboard/user/basket/basket.component';
import { PaymentComponent } from './dashboard/user/payment/payment.component';
import { AdminComponent } from './dashboard/admin/admin.component';
import { CategoryComponentAdmin } from './dashboard/admin/category/category.component';
import { AddCategoryComponent } from './dashboard/admin/category/add-category/add-category.component';
import { UpdateCategoryComponent } from './dashboard/admin/category/update-category/update-category.component';
import { ViewUsersComponent } from './dashboard/admin/view-users/view-users.component';
import { AuthGuard } from './auth.guard';



const Approuter: Routes = [

  {
    path: 'site',
    component: SiteComponent,
    children: [

      {
        path: '',
        component: SlidshowComponent,
        outlet: 'site'
      },

      {
        path: 'about',
        component: AboutComponent,
        outlet: 'site'
      },

      {
        path: 'services',
        component: ServicesComponent,
        outlet: 'site'
      },

      {
        path: 'login',
        component: LoginComponent,
        outlet: 'site'
      }

    ]
  },

  {
    path: '',
    redirectTo: '/site',
    pathMatch: 'full'
  },

  //Dashboard

  {
    path: 'dashboard',
    canActivate: [AuthGuard],
    component: DashboardComponent,
    children: [

      //User Routs

      {
        path: 'user',
        component: UserComponent,
        outlet: 'user'
      },

      {
        path: 'Schemes/:name',
        component: FundsComponent,
        outlet: 'user'
      },
      {
        path: 'category',
        component: CategoryComponent,
        outlet: 'user'
      },
      {
        path: 'navhis/:SchemeName',
        component: NavhistoryComponent,
        outlet: 'user'
      },
      {
        path: 'basket',
        component: BasketComponent,
        outlet: 'user'
      },
      {
        path: 'payment',
        component: PaymentComponent,
        outlet: 'user'
      },
      // Admin Routs
      {
        path: 'admin',
        component: AdminComponent,
        outlet: 'admin'
      },
      {
        path: 'categoryadmin',
        component: CategoryComponentAdmin,
        outlet: 'admin'
      },
      {
        path: 'addcategoryadmin',
        component: AddCategoryComponent,
        outlet: 'admin'
      },
      {
        path: 'updateCategory/:_id',
        component: UpdateCategoryComponent,
        outlet: 'admin'
      },
      {
        path: 'viewusers',
        component: ViewUsersComponent,
        outlet: 'admin'
      },
      

    ]
  },
  



];

@NgModule({
  imports: [
    RouterModule.forRoot(Approuter)
  ],
  exports: [
    RouterModule
  ],
  declarations: []
})
export class routing { }


