import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'nameFilter'})
export class NameSearchPipe implements PipeTransform {
    transform(value: any[], args: string): any[] {
        let filter: string = args ? args.toLocaleLowerCase() : null;
        return filter ? value.filter((Category) =>
        Category.name.toLocaleLowerCase().startsWith(filter) != false) : value;
    }
}

@Pipe({name: 'FunddataFilter'})
export class FundSearchPipe implements PipeTransform {
    transform(value: any[], args: string): any[] {
        let filter: string = args ? args.toLocaleLowerCase() : null;
        return filter ? value.filter((Funddata) =>
        Funddata.scheme_name.toLocaleLowerCase().startsWith(filter) != false) : value;
    }
}

@Pipe({name: 'BasketdataFilter'})
export class BasketSearchPipe implements PipeTransform {
    transform(value: any[], args: string): any[] {
        let filter: string = args ? args.toLocaleLowerCase() : null;
        return filter ? value.filter((basketdetail) =>
        basketdetail.Scheme_name.toLocaleLowerCase().startsWith(filter) != false) : value;
    }
}



