import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { FileUploader } from 'ng2-file-upload';
import { LogindataService } from '../../../../logindata.service'

@Component({
  selector: 'app-add-category',
  templateUrl: './add-category.component.html',
  styleUrls: ['./add-category.component.css']
})
export class AddCategoryComponent implements OnInit {

  enableedit: boolean = false;
  category: any = {};
  id: string;

  constructor(private http: HttpClient, private router: Router, private route: ActivatedRoute, private _logindata: LogindataService) { }

   public uploader:FileUploader = new FileUploader({url:'https://mutual-fund.herokuapp.com/users/profile'});
  ngOnInit() {


  }

  onSubmit(formValue: any) {

     this.uploader.queue[0].file.name=formValue.Category_name;
     console.log(this.uploader);
    this.uploader.queue[0].upload();
    let newCategory = {
      Category_name: formValue.Category_name,
      Category_description: formValue.Category_description
    };
    this.http.post('https://mutual-fund.herokuapp.com/users/createCategory', newCategory).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log("Error occured");
      }
    );
    //  console.log("newCategory = " + newCategory.Category_name);
    setTimeout(() => {
    window.alert("New Category Added sucessfully..!");
    this.router.navigate(['dashboard', {outlets: {'admin': ['categoryadmin']}}]);
  },1000);


  }

 
}



