import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Observable} from 'rxjs/Rx';
import { LogindataService } from "../logindata.service";
import { Router, ActivatedRoute, Params } from '@angular/router';
import { BasketSchema } from '../app.BasketInterface';

 
@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
count: number=0;
userid: any;
Adminuser: any;

  constructor(private http: HttpClient, private route: ActivatedRoute,  private router: Router,private _loginService: LogindataService) { 
    
  }

  ngOnInit() {
    this.Adminuser=this._loginService.getAdminlogin();
    this.userid=this._loginService.getUseremail();
    // Observable.interval(2000).subscribe(x => {
       
      // console.log(this.userid);
      this.http.get('https://mutual-fund.herokuapp.com/users/viewbasket/'+this.userid ).subscribe((data:any) => {

        data.forEach(element => {
          this.count++;
        });
        this._loginService.putaddToBasket(this.count);
        //console.log(this.basketdetails);
      },
      err => console.log(err)
    );
    
       Observable.interval(2000).subscribe(x => {
        this.count=this._loginService.getaddToBasket();
       });
  }

  logout(){
    this.router.navigate(['/site', {outlets: {site: ['login']}}]);
    //window.location.reload();
  }

  

}



