import { Component, OnInit } from '@angular/core';
import { LogindataService } from "../../../logindata.service";
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { BasketSchema } from '../../../app.BasketInterface';

@Component({
  selector: 'app-basket',
  templateUrl: './basket.component.html',
  styleUrls: ['./basket.component.css']
})
export class BasketComponent implements OnInit {
  nameFilter: any;
  basketdetails: any = {};
  purchasebasketdetails: any = {};
  useremail: any;
  BasketdataFilter: any;
  orderId :number;

  constructor(private http: HttpClient, private route: ActivatedRoute, private router: Router, private _loginService: LogindataService) {

  }

  ngOnInit() {
    this.useremail = this._loginService.getUseremail();
    console.log(this.useremail);
    this.http.get('https://mutual-fund.herokuapp.com/users/viewbasket/' + this.useremail).subscribe((data: any) => {
      //console.log(data.data);
      this.basketdetails = data;
    },
      err => console.log(err)
    );
  }

  update(Scheme_unit: any, Scheme_number: any, User_email: any) {

    let updatebasket = {

      Scheme_unit: Scheme_unit,
      };
    this.http.post('https://mutual-fund.herokuapp.com/users/updatebasket/'+ Scheme_number+'/'+ User_email, updatebasket).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log("Error occured ");
      }
    );
    this.router.navigate(['dashboard', {outlets: { user: ['basket']}}]);
  }

  purchase() {
    this.orderId = (Math.random()*100000)+1;


    this.router.navigate(['dashboard', { outlets: { user: ['payment'] } }]);


  }

  delete( Scheme_number: any, User_email: any) {
    this.http.get('https://mutual-fund.herokuapp.com/users/deletebasket/'+ Scheme_number+'/'+ User_email).subscribe((data: any) => {
           console.log("Deleted sucessfully");
           
    },
      err => console.log(err)
    );
    this._loginService.putremoveFromBasket();
    window.alert(Scheme_number+" Item removed from cart by new Fund...! ")
    this.router.navigate(['dashboard', {outlets: { user: ['category']}}]);
  }
}



