import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import { ChartsModule } from 'ng2-charts';
import { FileSelectDirective, FileDropDirective } from 'ng2-file-upload';



import { AppComponent } from './app.component';
import { NavComponent } from './nav/nav.component';
import { SlidshowComponent } from './slidshow/slidshow.component';
import { SiteComponent } from './site/site.component';
import { routing } from './app.routing';
import { AboutComponent } from './site/about/about.component';
import { ServicesComponent } from './site/services/services.component';
import { LoginComponent } from './site/login/login.component';
import { RegisterComponent } from './site/register/register.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { UserComponent } from './dashboard/user/user.component';
import { FundsComponent } from './dashboard/user/funds/funds.component';
import { CategoryComponent } from './dashboard/user/funds/category/category.component';
import { NavhistoryComponent } from './dashboard/user/navhistory/navhistory.component';
import { NameSearchPipe } from './Search/searchCategory.pipe';
import { FundSearchPipe } from './Search/searchCategory.pipe';
import { BasketSearchPipe } from './Search/searchCategory.pipe';
import { LogindataService } from './logindata.service';
import { BasketComponent } from './dashboard/user/basket/basket.component';
import { PaymentComponent } from './dashboard/user/payment/payment.component';
import { AdminComponent } from './dashboard/admin/admin.component';
import { CategoryComponentAdmin } from './dashboard/admin/category/category.component';
import { AddCategoryComponent } from './dashboard/admin/category/add-category/add-category.component';
import { UpdateCategoryComponent } from './dashboard/admin/category/update-category/update-category.component';
import { ViewUsersComponent } from './dashboard/admin/view-users/view-users.component';
import { AuthGuard } from './auth.guard';



@NgModule({
  declarations: [
    AppComponent,
    NavComponent,
    SlidshowComponent,
    SiteComponent,
    AboutComponent,
    ServicesComponent,
    LoginComponent,
    RegisterComponent,
    DashboardComponent,
    UserComponent,
    FundsComponent,
    CategoryComponent,
    NavhistoryComponent,
    NameSearchPipe,
    FundSearchPipe,
    BasketSearchPipe,
    BasketComponent,
    PaymentComponent,
    AdminComponent,
    CategoryComponentAdmin,
    AddCategoryComponent,
    FileSelectDirective,
    UpdateCategoryComponent,
    ViewUsersComponent,
  ],
  imports: [
    BrowserModule, routing, FormsModule, HttpClientModule, HttpModule,ChartsModule
  ],
  providers: [LogindataService,AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



