import { Component, OnInit } from '@angular/core';
import { LogindataService } from "../../logindata.service";
import { Router, ActivatedRoute, Params } from '@angular/router';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {

 
  editbaseinfo: boolean=false;
  

  constructor(private http: HttpClient, private router: Router,private _loginService: LogindataService) { }

  userid: string;
  userdetails: any={};


  ngOnInit() {

    this.userid=this._loginService.getUsername();
     //console.log(this.userid);
     this.http.get('https://mutual-fund.herokuapp.com/users/viewProfile/'+this.userid).subscribe((data:any[]) => {
      // console.log(data);
       this.userdetails = data;
     },
     err => console.log(err)
   );

  }

  onSubmitbase(formValue: any) {

    console.log(formValue);
    this.editbaseinfo=false;
    let updatedbaseinfo = {
        
      name: formValue.name,
      DOB: formValue.DOB,
      emailID: formValue.emailID,
      password: formValue.pwd,
      gender: formValue.gender,
      mobileNumber: formValue.mobileNumber,
      
         
        };
        this.http.post('https://mutual-fund.herokuapp.com/users/updatebaseinfo/'+this.userid, updatedbaseinfo ).subscribe(
          res => {
          console.log(res);
        },
        err => {
          console.log("Error occured ");
        }
      );
    
    this.router.navigate(['dashboard', {outlets: {'admin': ['admin']}}]);

  }

  baseedit(){
    this.editbaseinfo=true;
  }

  


}



