import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Location } from "@angular/common";
import { Router, ActivatedRoute, Params } from '@angular/router';
import { LogindataService } from '../../../logindata.service';


@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponentAdmin implements OnInit {
  Categorys: any[];

  constructor(private http: HttpClient, private router: Router, private location: Location, private _logindata: LogindataService) {
  }
  ngOnInit() {
    this.http.get('https://mutual-fund.herokuapp.com/users/viewCategory').subscribe((data: any) => {
      // console.log(data);
      this.Categorys = data;
    },
      err => console.log(err)
    );
  }

  updateCategory(category: any) {

    this._logindata.putupdateCategory('true');
    this.router.navigate(['dashboard', { outlets: { 'admin': ['updateCategory', category] } }]);
  }

  deleteCategory(category: any) {


    this.http.get('https://mutual-fund.herokuapp.com/users/deleteCategory/'+category).subscribe((data: any) => {
      //     console.log("Deleted sucessfully");
    },
      err => console.log(err)
    );

    
      this.router.navigate(['dashboard', {outlets: {'admin': ['addcategoryadmin']}}]);
    
    


  }

}



