import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {Location, LocationStrategy, PathLocationStrategy} from '@angular/common';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-navhistory',
  templateUrl: './navhistory.component.html',
  styleUrls: ['./navhistory.component.css']
})
export class NavhistoryComponent implements OnInit {

  SchemeHis: any[];
  SchemeID: any;
  public lineChartData: Array<any> = [];
  public lineChartLabels: Array<any> = [];
  public lineChartOptions: any;
  public lineChartColors: Array<any> = [];
  public lineChartLegend: boolean;
  public lineChartType: string;

  constructor(private route: ActivatedRoute, private router: Router, private http: HttpClient,location: Location) { }

  back(){
    window.history.back();
  }

  ngOnInit() {
    this.route.params.forEach((params: Params) => {
      
      this.SchemeID = params['SchemeName'];
  });
    this.http.get('https://mfutil.com/api/v1/mutualfunds/history/'+this.SchemeID).subscribe((data: any) => {

      this.SchemeHis = data.data;
      console.log(this.SchemeHis.length);
     

    },
      err => console.log(err)
    );
    setTimeout(() => {
      // lineChart
      this.lineChartData = [
        { data: [this.SchemeHis[(this.SchemeHis.length)-1].nav, this.SchemeHis[(this.SchemeHis.length)-2].nav, this.SchemeHis[(this.SchemeHis.length)-3].nav, this.SchemeHis[(this.SchemeHis.length)-4].nav, this.SchemeHis[(this.SchemeHis.length)-5].nav], label: 'Past 5 days Scheme history' }

      ];
     
      this.lineChartLabels = [this.SchemeHis[this.SchemeHis.length-1].date, this.SchemeHis[this.SchemeHis.length-2].date, this.SchemeHis[this.SchemeHis.length-3].date, this.SchemeHis[this.SchemeHis.length-4].date, this.SchemeHis[this.SchemeHis.length-5].date];
      this.lineChartOptions = {
        responsive: true
      };
      this.lineChartColors = [
        { // grey
          backgroundColor: 'rgba(148,159,177,0.2)',
          borderColor: 'rgba(148,159,177,1)',
          pointBackgroundColor: 'rgba(148,159,177,1)',
          pointBorderColor: '#fff',
          pointHoverBackgroundColor: '#fff',
          pointHoverBorderColor: 'rgba(148,159,177,0.8)'
        }
      ];
      this.lineChartLegend = true;
      this.lineChartType = 'line';

    },
      5000);

  }


}



