import { Component, OnInit } from '@angular/core';
import { LogindataService } from "../../logindata.service";
import { Router, ActivatedRoute, Params } from '@angular/router';
import { HttpClient } from '@angular/common/http';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent implements OnInit {


  editbaseinfo: boolean=false;
  accbaseinfo: boolean=false;

  constructor(private http: HttpClient, private router: Router,private _loginService: LogindataService) { }

  userid: string;
  userdetails: any={};


  ngOnInit() {

    this.userid=this._loginService.getUsername();
     //console.log(this.userid);
     this.http.get('https://mutual-fund.herokuapp.com/users/viewProfile/'+this.userid).subscribe((data:any[]) => {
      // console.log(data);
       this.userdetails = data;
     },
     err => console.log(err)
   );

  }

  onSubmitbase(formValue: any) {

    console.log(formValue);
    this.editbaseinfo=false;
    let updatedbaseinfo = {
        
      name: formValue.name,
      DOB: formValue.DOB,
      emailID: formValue.emailID,
      password: formValue.pwd,
      gender: formValue.gender,
      mobileNumber: formValue.mobileNumber,
      
         
        };
        this.http.post('https://mutual-fund.herokuapp.com/users/updatebaseinfo/'+this.userid, updatedbaseinfo ).subscribe(
          res => {
          console.log(res);
        },
        err => {
          console.log("Error occured ");
        }
      );
    
      this.router.navigate(['dashboard', {outlets: {'user': ['user']}}]);

  }

  baseedit(){
    this.editbaseinfo=true;
  }

  onSubmitaccbase(formValue: any) {

    console.log(formValue);
    this.accbaseinfo=false;
    let updatedaccinfo = {
        
      Bank_name: formValue.Bank_name,
      ACC_Number: formValue.ACC_Number,
      IFSC: formValue.IFSC,
      ACC_type: formValue.ACC_type
      
         
        };
        this.http.post('https://mutual-fund.herokuapp.com/users/updateaccinfo/'+this.userid, updatedaccinfo ).subscribe(
          res => {
          console.log(res);
        },
        err => {
          console.log("Error occured ");
        }
      );
    
      this.router.navigate(['dashboard', {outlets: {'user': ['user']}}]);

  }

  accedit(){
    this.accbaseinfo=true;
  }

}



