import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { LogindataService } from '../../../logindata.service'
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-funds',
  templateUrl: './funds.component.html',
  styleUrls: ['./funds.component.css']
})
export class FundsComponent implements OnInit {

  Funddatas: any[];
  basket: any[] = [];
  category: any = {};
  useremail: any;
  count: number = 0;
  FunddataFilter: any;

  constructor(private route: ActivatedRoute, private router: Router, private http: HttpClient, private _logindata: LogindataService) { }

  ngOnInit() {
    this.route.params.forEach((params: Params) => {

      this.category = params['name'];
    });
    this.http.get('https://mfutil.com/api/v1/mutualfunds/search/' + this.category).subscribe((data: any) => {
      //console.log(data.data);
      this.Funddatas = data.data;
    },
      err => console.log(err)
    );

    this.useremail = this._logindata.getUseremail();

  }
  add(nav: any, scheme_number: any, scheme_name: any, amc: any) {

    // this.http.get('https://mutual-fund.herokuapp.com/users/viewbasket/' + this.useremail).subscribe((data: any) => {
    //   //console.log(data.data);
    //   this.basket = data.data;
      
    // },
    //   err => console.log(err)
    // );
    //this.count++;

    this._logindata.putaddToBasket(1);
    let addBasket = {
      Scheme_unit: nav,
      Scheme_number: scheme_number,
      User_email: this.useremail,
      Scheme_name: scheme_name,
      AMC: amc
    };

    //create
    this.http.post('https://mutual-fund.herokuapp.com/users/createBasket', addBasket).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log("Error occured");
      }
    );


  }


}



